import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../views/Index.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Index',
    component: Index
  },
  
  {
    path: '/welcome',
    name: 'Welcome',
    component: ()=>import('../views/Welcome.vue')
  },

  {
    path: '/dashboard',
    name: 'Dashboard',
    component: ()=>import('../views/Dashboard.vue')
  },
]

const router = new VueRouter({
  routes
})

export default router
